@use 'base/variables' as v;
@use 'base/mixins' as m;
.header{
    position: relative;
    height: 85rem;
    background-image: linear-gradient(rgba(0,0,0,.63),rgba(0,0,0,.63)),url("/public/build/img/wallpaper.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    @include m.tablet {
        height: 100vh;
        align-items: center;
        justify-content: center;
    }
    .cajas{
        margin-top: 2rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
        @include m.desktop { 
            flex-direction: row;
        }
        @include m.tablet { 
            margin-top: 0;
        }
        .banner{
            margin-bottom: 1.8rem;
            img{
                width: 15rem;
                height: 10rem;
            }
            @include m.tablet { 
                margin-bottom: 5rem;
                img{
                    width: 25rem;
                    height: 15rem;
                }
            }
            @include m.desktop { 
                margin-bottom: 0;
                img{
                    width: 50rem;
                    height: 30rem;
                }
            }
        }
    }
}
.login{
    h1{
        color: v.$blanco;
        font-weight: 900;
        text-align: center;
    }
    .formulario{
        legend{
            color: v.$blanco;
            margin-bottom: 2rem;
            width: 100%;
            text-align: center;font-weight: 900;
            font-size: 2.4rem;
        }
        .campo{

            margin-bottom: 1rem;
            padding: 1rem;

            label{
                color: v.$blanco;  
                font-weight: 700;   
            }
            input{
                border: none;
                padding: 1rem;
                border-radius: 1rem;
                background-color:#199DC9;
                color: v.$blanco;
                &::placeholder{
                    color: darken(v.$grisClaro, 15%);
                }
            }
            textarea{
                padding: .5rem;
                    border: none;
                    background-color:#199DC9;
                    color: v.$blanco;
                    border-radius: 1rem;
                    height: 10rem;
                    resize: none;
                    &::placeholder{
                        color: darken(v.$grisClaro, 15%);
                    }
            }
            @include m.tablet { 
                display: flex;
                align-items: flex-start;
                //
                label{ 
                    flex: 0 0 10rem;   
                }
                input{
                    flex: 1;
                }
                textarea{
                    flex: 1;
                }
            }
        }
        .enviar{
            @include m.boton(#199DC9,v.$blanco);
                &.cursor{
                    opacity: .7;
                    &:hover{
                        cursor: not-allowed;
                    }
                }
        }
        .errores{
            height: auto;
            padding: .5rem;
            margin-bottom: 1rem;
            p{
                padding: .5rem;
                margin: 0;
                background-color: v.$rojo;
                color:v.$blanco;
                font-size: 2rem;
                text-align: center;
                font-weight: 700;
            }    
                
        }
    }
}
footer{
    width: 100%;
    background-color:#199DC9;
    margin-top: 2rem;
    position: absolute;
    bottom: 0;
}
.footer{
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-bottom: 1rem;
    @include m.tablet { 
        padding-bottom: 0;
        flex-direction: row;
        justify-content: space-between;   
    }
    
    p{
        color: v.$blanco;
        font-weight: 900;
    }
    .redesSociales{
        display: flex;
        img{
            width: 3rem;
            height: 3rem;
            margin-right: .5rem;
        }
    }
}