body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 3rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  position: relative;
  height: 85rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.63), rgba(0, 0, 0, 0.63)), url("/public/build/img/wallpaper.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
@media (min-width: 768px) {
  .header {
    height: 100vh;
    align-items: center;
    justify-content: center;
  }
}
.header .cajas {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
@media (min-width: 1024px) {
  .header .cajas {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .header .cajas {
    margin-top: 0;
  }
}
.header .cajas .banner {
  margin-bottom: 1.8rem;
}
.header .cajas .banner img {
  width: 15rem;
  height: 10rem;
}
@media (min-width: 768px) {
  .header .cajas .banner {
    margin-bottom: 5rem;
  }
  .header .cajas .banner img {
    width: 25rem;
    height: 15rem;
  }
}
@media (min-width: 1024px) {
  .header .cajas .banner {
    margin-bottom: 0;
  }
  .header .cajas .banner img {
    width: 50rem;
    height: 30rem;
  }
}

.login h1 {
  color: #FFFFFF;
  font-weight: 900;
  text-align: center;
}
.login .formulario legend {
  color: #FFFFFF;
  margin-bottom: 2rem;
  width: 100%;
  text-align: center;
  font-weight: 900;
  font-size: 2.4rem;
}
.login .formulario .campo {
  margin-bottom: 1rem;
  padding: 1rem;
}
.login .formulario .campo label {
  color: #FFFFFF;
  font-weight: 700;
}
.login .formulario .campo input {
  border: none;
  padding: 1rem;
  border-radius: 1rem;
  background-color: #199DC9;
  color: #FFFFFF;
}
.login .formulario .campo input::placeholder {
  color: #c2c9cc;
}
.login .formulario .campo textarea {
  padding: 0.5rem;
  border: none;
  background-color: #199DC9;
  color: #FFFFFF;
  border-radius: 1rem;
  height: 10rem;
  resize: none;
}
.login .formulario .campo textarea::placeholder {
  color: #c2c9cc;
}
@media (min-width: 768px) {
  .login .formulario .campo {
    display: flex;
    align-items: flex-start;
  }
  .login .formulario .campo label {
    flex: 0 0 10rem;
  }
  .login .formulario .campo input {
    flex: 1;
  }
  .login .formulario .campo textarea {
    flex: 1;
  }
}
.login .formulario .enviar {
  background-color: #199DC9;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.login .formulario .enviar:hover {
  cursor: pointer;
  background-color: #137a9c;
}
@media (min-width: 768px) {
  .login .formulario .enviar {
    width: auto;
  }
}
.login .formulario .enviar.cursor {
  opacity: 0.7;
}
.login .formulario .enviar.cursor:hover {
  cursor: not-allowed;
}
.login .formulario .errores {
  height: auto;
  padding: 0.5rem;
  margin-bottom: 1rem;
}
.login .formulario .errores p {
  padding: 0.5rem;
  margin: 0;
  background-color: #ff0000;
  color: #FFFFFF;
  font-size: 2rem;
  text-align: center;
  font-weight: 700;
}

footer {
  width: 100%;
  background-color: #199DC9;
  margin-top: 2rem;
  position: absolute;
  bottom: 0;
}

.footer {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .footer {
    padding-bottom: 0;
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer p {
  color: #FFFFFF;
  font-weight: 900;
}
.footer .redesSociales {
  display: flex;
}
.footer .redesSociales img {
  width: 3rem;
  height: 3rem;
  margin-right: 0.5rem;
}

.aparecer {
  display: none;
}

.sk-folding-cube {
  margin: 20px auto;
  width: 40px;
  height: 40px;
  position: relative;
  -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}

.sk-folding-cube .sk-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

.sk-folding-cube .sk-cube:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #199DC9;
  -webkit-animation: sk-foldCubeAngle 2.4s infinite linear both;
  animation: sk-foldCubeAngle 2.4s infinite linear both;
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}

.sk-folding-cube .sk-cube2 {
  -webkit-transform: scale(1.1) rotateZ(90deg);
  transform: scale(1.1) rotateZ(90deg);
}

.sk-folding-cube .sk-cube3 {
  -webkit-transform: scale(1.1) rotateZ(180deg);
  transform: scale(1.1) rotateZ(180deg);
}

.sk-folding-cube .sk-cube4 {
  -webkit-transform: scale(1.1) rotateZ(270deg);
  transform: scale(1.1) rotateZ(270deg);
}

.sk-folding-cube .sk-cube2:before {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

.sk-folding-cube .sk-cube3:before {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s;
}

.sk-folding-cube .sk-cube4:before {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s;
}

@-webkit-keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
@keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}